body {
  background-color: #F15445;
}

.app {
  width: 80%;
  min-height: 300px;
  background-color: white;
  margin: 150px auto;
  padding: 10px 0;
}

.app .container {
  width: 90%;
  margin: 0 auto;
  padding-top: 30px;
}

.app .container .title {
  text-align: center;
  color: white;
  background-color: #F15445;
  padding: 5px 0;
}

.add-item input,
.item .updated-course-name {
  width: 80%;
  background-color: #E9E7E9;
  border: 0;
  outline: 0;
  padding: 5px;
}

.add-item button {
  width: 18%;
  padding: 5px;
  color: white;
  background-color: #46B359;
  cursor: pointer;
  border: 0;
  font-weight: bold;
}

.items-list {
  margin-top: 20px;
}

.item {
  margin-bottom: .5rem;
}

.item .course-name {
  width: 60%;
  padding: 5px;
  display: inline-block;
}

.item button {
  width: 17%;
  color: white;
  cursor: pointer;
  padding: 5px;
  border: 0;
}

.item .edit {
  background-color: #6B696B;
}

.item .delete {
  background-color: #F15445;
  float: right;
}

.item .update {
  width: 18%;
  background-color: #46B359;
  margin: 0;
}